.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


.room-container {
  display: flex;
  flex-wrap: wrap;
}

.room {
  padding: 0px 20px;
  border: 1px grey solid;
  border-radius: 4px;
  margin: 10px;
}

.room-container > .room:nth-child(even) {
  background-color: #353942;
}

.room p {
  display: flex;
  justify-content: space-between;
  min-width: 180px;
}

.room p label {
  color: darkgrey;
}